<template>
    <div id="divMainFormData" v-cloak>
        <loader :is-visible="isLoading"></loader>
        <section id="main-content" class="main-content">
            <section class="main-content">              
                <div class="left-menu-tab div-click-event">
                        <div class="col-md-12 p-0 ">
                            <div class="theme-primary partition">
                                <span class="p-name text-white textelipsis">{{priceBookName}}</span>
                            </div>
                        </div>
                        <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab" id="myTab" role="tablist">
                            <li class="nav-item">
                                <a 
                                    class="nav-link  left-menu"
                                    :class="{'active':activeKey==1}"
                                    href="javascript:void(0)" 
                                    data-bs-toggle="tab"  
                                    data-bs-target="#tab-detail-content" 
                                    role="tab" 
                                    id="tab-Details" 
                                    v-on:click="loadData('timeline');DisableButton()">
                                    <em class="fa fa-list" aria-hidden="true" aria-selected="true"></em>{{('Detail')}}
                                </a>
                            </li>                        
                            <li class="nav-item">
                                <a 
                                    class="nav-link left-menu"
                                    :class="{'active':activeKey==2}" 
                                    data-bs-toggle="tab" 
                                    data-bs-target="#tab-notes-content"
                                    role="tab" 
                                    href="javascript:void(0)" 
                                    id="tab-notes" 
                                    v-on:click="loadData('notes')">
                                    <em class="fa fa-file-text" aria-hidden="true" aria-selected="false"></em>{{('Note')}}
                                </a>
                            </li>
                            <li class="nav-item">
                                <a 
                                    class="nav-link left-menu" 
                                    :class="{'active':activeKey==3}"
                                    data-bs-target="#tab-attachment-content"
                                    data-bs-toggle="tab"
                                    role="tab" 
                                    href="javascript:void(0)" 
                                    id="tab-attachment" 
                                    v-on:click="loadData('attachments')">
                                    <em class="fa fa-file" aria-hidden="true" aria-selected="false"></em>{{('Attachments')}}
                                </a>
                            </li>
                            <li class="nav-item">
                                <a 
                                    class="nav-link left-menu" 
                                    :class="{'active':activeKey==4}" 
                                    data-bs-target="#tab-openactivities-content"
                                    href="javascript:void(0)"
                                    id="tab-openactivities"
                                    data-bs-toggle="tab"
                                    role="tab"  
                                    v-on:click="loadData('activities')">
                                    <em class="fa fa-calendar" aria-hidden="true"></em>{{('Activities')}}
                                </a>
                            </li>
                            <li class="nav-item">
                                <a 
                                    class="nav-link left-menu" 
                                    :class="{'active':activeKey==5}" 
                                    data-bs-toggle="tab" 
                                    role="tab" 
                                    href="javascript:void(0)"
                                    id="tab-products" 
                                    v-on:click="loadData('products')">
                                    <em class="fa fa-shopping-cart" aria-hidden="true" aria-selected="false"></em>{{('Products')}}
                                </a>
                            </li>
                        </ul>
                </div>
                <div class="right-content">
                        <div class="tab-pane mb-2">
                            <div class="theme-primary partition p-actions-expand">
                                <span class="p-name text-white" id="span_ForSelectedTabName"></span>
                                <span class="p-actions float-right">
                                   
                                    <a 
                                        href="javascript:;" 
                                        v-bind:class="{'p-action-btn text-white':true, 'disabled':false}" 
                                        title="" 
                                        data-toggle-tooltip="tooltip" 
                                        data-original-title="Card View" 
                                        v-on:click="NoteListType = 'card'" 
                                        v-if="isNotes && NoteListType == 'list'">
                                        <em class="fa fa-th-large"></em><br />{{$t('CardView')}}</a>
                                    <a 
                                        href="javascript:;" 
                                        v-bind:class="{'p-action-btn text-white':true, 'disabled':false}" 
                                        title="" 
                                        data-toggle-tooltip="tooltip" 
                                        data-original-title="List View" 
                                        v-on:click="NoteListType = 'list'" 
                                        v-if="isNotes && NoteListType != 'list'">
                                        <em class="fa fa-list"></em><br />{{$t('ListView')}}</a>
                                    <a 
                                        href="javascript:;" 
                                        v-bind:class="{'p-action-btn text-white':true, 'disabled':false}" 
                                        title="" 
                                        id="ancaddnotesloaddata" 
                                        data-toggle-tooltip="tooltip" 
                                        data-original-title="Add Notes" 
                                        v-on:click="openShowNotePopup()" v-if="isNotes">
                                        <em class="fa fa-plus"></em><br />{{$t('AddNotes')}}</a>
                                    <a 
                                        href="javascript:;" 
                                        v-bind:class="{'p-action-btn text-white':true}" 
                                        :title="$t('UploadFile')" 
                                        v-on:click="openCRMAttachmentModal(null);" 
                                        v-if="isAttachment">
                                        <em class="fa fa-plus"></em><br />{{$t('AddAttachment')}}</a>
                                    <a 
                                        href="javascript:void(0)" 
                                        v-bind:class="{'p-action-btn text-white btn_add':true}" 
                                        title="" 
                                        id="" 
                                        v-on:click="openAddTaskModel()" 
                                        data-original-title="Add Task" 
                                        v-if="isActivities">
                                        <em class="fa fa-plus"></em><br />{{$t('AddTask')}}</a>
                                    <a 
                                        href="javascript:void(0)" 
                                        v-bind:class="{'p-action-btn text-white btn_follow':true}" 
                                        title="" 
                                        id="" 
                                        v-on:click="openAddNewEvent()" 
                                        data-original-title="Follow Up" 
                                        v-if="isActivities">
                                        <em class="fa fa-share"></em><br />{{$t('FollowUp')}}
                                    </a>
                                    <a 
                                        href="javascript:void(0)" 
                                        v-bind:class="{'p-action-btn text-white btn_call':true}" 
                                        title="" 
                                        id="" 
                                        v-on:click="openCallEvent()" 
                                        data-original-title="Schedule Call" 
                                        v-if="isActivities">
                                        <em class="fa fa-phone"></em><br />{{$t('ScheduleCall')}}
                                    </a>                                    
                                    <RouterLink
                                        :to="'/CRM/PriceBookListing'" class="p-action-btn text-white float-right">
                                        <em class="fa fa-angle-double-left pr-2"></em>
                                        <br />
                                        {{ $t('BacktoList') }}
                                    </RouterLink>
                                    <RouterLink :to="editUrl"
                                        v-if="stausName != 'Closed'"
                                        class="p-action-btn text-white">
                                        <em class="fa fa-pencil"></em><br/> {{ $t('Edit') }} 
                                    </RouterLink>
                                </span>
                                <span class="collapse-head-btns">
                                    <a href="javascript:;" class="collapse-btns"><em class="fa fa-angle-up text-white"></em></a>
                                </span>
                            </div>
                        </div>
                        <div class="tab-pane mb-0">
                        <div class="tab-content fsmtabcontrolMobileView" id="myTabContent">
                            <div class="expand-list">
                                <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                                    <em class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></em>
                                </a>
                            </div>
                            <div 
                                class="tab-pane p-0 pb-2 border-0" 
                                :class="{ 'active show': activeKey == 1 }" 
                                role="tabpanel" 
                                id="tab-detail-content" aria-labelledby="tab-detail-content">                                
                                <div class="listing">
                                    <div class="row">
                                        <div class="col-md-12">
                                            <div class="card mb-3 probx">
                                                <div class="card-header">
                                                    <h2>{{$t('Details')}}</h2>
                                                    <div class="cont_right ">
                                                        <div class="bar">
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="card-body">
                                                    <div class="panel1" v-for="(group,index) in LeftViewData" :key="index">
                                                        <div class="heading-border">
                                                            <h5 class="h5 mb-2"><span>{{$t(group.group_name,'')}}</span></h5>
                                                        </div>
                                                        <div class="row">
                                                            <div class="col-md-6 col-lg-3" v-for="(item, index) in group.LeftViewData" :key="index">
                                                                <div class="form-group">
                                                                    <label>
                                                                        <strong>{{$t(item.name, item.display_name)}}:</strong>
                                                                        <span class="answer rightlabeltext ml-1" v-if="item.dt_code == 'date' || item.dt_code == 'datetime'">{{item.value | formatDate}}</span>
                                                                        <span class="answer rightlabeltext ml-1" v-if="item.dt_code == 'decimal'">{{isBlank(item.value)?"0.00":item.value | currency}}</span>
                                                                        <span class="answer rightlabeltext ml-1" v-if="item.dt_code != 'date' && item.dt_code != 'datetime'  && item.dt_code != 'decimal' && item.picklist_options != 'Lookup' && (item.name != 'description' || (item.name == 'description' && isBlank(item.value)))">{{ isBlank(item.value)?"--":(item.value)}}</span>
                                                                        <crmreadmore v-if="item.name == 'description'" v-bind:data="item.value" heading="Description" length="100" class="ml-1">{{item.value }}</crmreadmore>
                                                                        <span class="answer rightlabeltext ml-1" v-if="item.dt_code != 'date' && item.dt_code != 'datetime'  && item.dt_code != 'decimal' && item.picklist_options == 'Lookup'">  {{isBlank(item.value)?"--":(FilterDDLandNormalValue(item.value))}}</span>
                                                                    </label>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="card mb-3 probx">
                                                <div class="card-header">
                                                    <h2>{{$t("Timeline")}}</h2>
                                                </div>
                                                <div class="card-body">                                                   
                                                    <crmtimeline v-bind:timelinedata="timelinedata"></crmtimeline>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>                           
                            <div 
                                class="tab-pane fade p-0 pb-2 border-0" 
                                id="tab-notes-content" 
                                role="tabpanel" 
                                aria-labelledby="tab-notes-content"
                                :class="{'active show':activeKey==2}">
                                <crmnote 
                                    v-if="noteData"
                                    v-bind:notedata="noteData" 
                                    :userid="userid" 
                                    :companyid="companyId" 
                                    :sourceid="$route.params.id" 
                                    :modulename="$route.params.moduleName"  
                                    :submodulecode="$route.params.subModuleCode"                                  
                                    v-bind:listtype="NoteListType"
                                    :showNotePopUp="isShowNotePopup"
                                    callfrom="quickViewAndNoteJS">
                                </crmnote>
                            </div>
                            <div 
                                class="tab-pane fade p-0 pb-2 border-0"
                                :class="{'active show':activeKey==3}"
                                id="tab-attachment-content" 
                                role="tabpanel" 
                                aria-labelledby="tab-attachment-content">
                                <crmattachment 
                                    v-bind:attachmentdata="attachmentData" 
                                    :sourceid="$route.params.id">
                                </crmattachment>
                            </div>
                            <div 
                                class="tab-pane fade p-0 border-0 listing"
                                :class="{'active show':activeKey==4}" 
                                id="tab-openactivities-content" 
                                role="tabpanel" 
                                aria-labelledby="tab-openactivities-content"
                                >
                                <div class="table-responsive" v-if="!noRecordForTab">
                                    <table class="table table-bordered mb-0" id="table-openactivities-content" aria-describedby="activitescontent">
                                        <thead class="thead-bg">
                                            <tr>
                                                 <th scope="col"><span>{{$t('Subject')}}</span></th>
                                                 <th scope="col"><span>{{$t('Activity')}}{{$t('Type')}}</span></th>
                                                 <th scope="col"><span>{{$t('Activity')}}{{$t('ScheduleDate')}}</span></th>
                                                 <th scope="col"><span>{{$t('Activity')}}{{$t('OWNER')}}</span></th>
                                                 <th scope="col"><span>{{$t('CreatedAt')}}</span></th>
                                                 <th scope="col"><span>{{$t('Status')}}</span></th>
                                                 <th scope="col" class="text-center"><span>{{$t('Action')}}</span></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(item,index) in activitiesData" :key="index">
                                                <td><a href="javascript:;" v-on:click="openActivityModal(item);">{{item.followup_notification_subject}}</a></td>
                                                <td>
                                                    <div v-if="item.activity_type === 'FollowUp'">
                                                        Follow up
                                                    </div>
                                                    <div v-else>
                                                        {{item.activity_type}}
                                                    </div>
                                                </td>
                                                <td>{{item.followup_notification_schedule | formatDate}}</td>
                                                <td>{{item.activity_owner}}</td>
                                                <td>{{item.created_at | formatDate }}</td>
                                                <td><span v-bind:class="{'px-2 bg-success text-white d-block text-center': (item.status_name == 'Active'),'px-2 bg-danger text-white d-block text-center': (item.status_name == 'Inactive')}">{{item.status_name}}</span></td>
                                                <td class="text-center single-action"><a href="javascript:;" title="Delete" v-on:click="DeleteActivity(item);"><em class="fa fa-trash action-icon text-danger"></em></a></td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <div v-if="noRecordForTab" class="text-center text-danger p-4 border no-record">{{$t('NoRecordfound')}}</div>
                            </div>
                            <div 
                                class="tab-pane fade p-0 border-0" 
                                :class="{ 'active show': activeKey == 5 }"
                                id="tab-products-content" 
                                role="tabpanel" 
                                aria-labelledby="tab-products-content">
                                <crmproduct 
                                    v-bind:productdata="productData" 
                                        :sourceid="$route.params.id" 
                                        :submodulecode="$route.params.subModuleCode" 
                                        :modulename="$route.params.moduleName" 
                                        :companyid="companyId">
                                </crmproduct>
                            </div>
                        </div>
                    </div>
                </div> 
            </section>
        </section>
        <modalattachment 
            v-if="showAttachmentModal" 
            @close="showAttachmentModal = false" 
            :userid="userid"
            :companyid="companyId" 
            :id="$route.params.id" 
            :modulename="$route.params.moduleName"
            :submodulecode="$route.params.subModuleCode" 
            :containername="containerName" imagesizedata="">
            <h5 slot="header" class="modal-title">{{$t('Attachments')}}</h5>
        </modalattachment>
        <modaltask 
            v-if="showAddTaskModal" 
            @close="showAddTaskModal = false, 
            modalTaskItem = null"
            :modalTaskItem="modalTaskItem">
            <h5 slot="header" class="modal-title">{{ $t('AddTask') }}</h5>
        </modaltask>
        <modalevent 
            v-if="showAddEventModal" 
            @close="showAddEventModal = false, 
            modalFollowUpItem = null"
            :modalFollowUpItem="modalFollowUpItem" v-bind:modulename="$route.params.moduleName"
            v-bind:submodulecode="$route.params.subModuleCode" 
            :sourceid="$route.params.id">
            <h5 slot="header" class="modal-title">{{ $t('FollowUp') }}</h5>
        </modalevent>
        <modalcall 
            v-if="showCallModal" 
            :modalCallItem="modalCallItem" 
            @close="showCallModal = false, 
            modalCallItem = null">
            <h5 slot="header" class="modal-title">{{ $t('Schedule') }} {{ $t('Call') }}</h5>
        </modalcall> 
    </div>  
</template>

<script>
import PriceBookDataService from "../../../services/DataService"; 
import crmtimeline from "../../../components/Common/Modal/crmtimeline.vue";
import crmnote from "../../../components/Common/Modal/crmnote.vue";
import crmattachment from "../../../components/Common/Modal/crmattachment.vue";
import crmproduct from "../../../components/Common/Modal/crmproduct.vue";
import modalattachment from "../../../components/Common/Modal/modalattachment.vue"
import modaltask from "../../../components/Common/Modal/modaltask.vue";
import modalcall from "../../../components/Common/Modal/modalcall.vue";
import modalevent from "../../../components/Common/Modal/modalevent.vue";
import crmreadmore from "../../../components/Common/Modal/crm-readmore.vue";

export default {
    components: {
        crmtimeline,
        crmnote,
        crmattachment,
        crmproduct,
        modalattachment,
        modaltask,
        modalevent,
        modalcall,
        crmreadmore     
    },
    data(){
        return{
            isLoader:false,
            activeKey: 1,
            isDetailPage: false,
            isActivities: false,
            isNotes: false,
            isAttachment: false,
            showProductModal: false,
            showAddTaskModal: false,
            showAddEventModal: false,
            showCallModal: false,
            showAttachmentModal: false,
           LeftViewData: [],
            RightViewData: null,
            attachmentData: null,
            activitiesData: null,
            timelinedata: [],
            productData: null,
            productHeaders: null,
            contactsData: null,
            contactsHeader: null,
            noRecordForTab: false,
            isShowNoteDialogue: false,
            noteData: [],
            priceBookName: '--',
            priceBookModel: '--',
            noteText: '',
            SortBy: '',
            SortExp: '',
            PageNumber: 1,
            PageSize: 50,
            modalTaskItem: null,
            modalFollowUpItem: null,
            modalCallItem: null,
            modalDeleteItem: null,
            NoteListType:'list',
            stausName: '',
            ApiURL: `id=${this.$route.params.id}&refId=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}`,
            maxsize:"@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(companyId, userid, 'CRM', 'CRMPerFileSize')",
            disallowedfiletypes:"@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(companyId, userid, 'CRM', 'CRMPerFileSize'))",
            userid: null,
            companyId: null,
            editUrl: `/CRM/ManagePriceBook/${this.$route.params.id}/${this.$route.params.moduleName}/${this.$route.params.subModuleCode}`,
            isShowNotePopup:false,
            containerName:null,
            isLoading:false
        }
    },
    created:function(){
        this.userid= this.GetUserInfo.ID;
        this.companyId= this.GetUserInfo.CompanyId;
        this.containerName= this.GetUserInfo.ContainerName;
        var VueObj=this;
        VueObj.FetchData();
    },
    updated(){ 
        var vm=this; 
        setTimeout(function () { vm.setHeaderForSelectedTab() }, 500);
    },   
  
    methods:{
        DeleteActivity: function (item) {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("DeleteAcitvities")
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        this.modalDeleteItem = item;
        this.DeleteActivityData(this.modalDeleteItem.followup_notification_id);
      }
    },
    DeleteActivityData: function (id) {
      var vm = this;
      if (id > 0) {
        talygen.confirm(vm.$t("ConfirmDelete"), function(result){
            if(result){
                var postJSON = {
              ids: id,
              moduleName: "CRM",
              subModuleCode: "CRM_PRICE_BOOKS",
              refCode: "DELETE_VIEW_ACTIVITY",
              refId: vm.sourceId
            };
            var objectPostString = JSON.stringify(postJSON);
            PriceBookDataService.CommonDelete(objectPostString).then(
              function (response) {
                if (response.data[0].Status == "Success") {
                  vm.ShowAlert(vm.$t('DeletedSuccess',[vm.$t('CONTRACTS')]),"Success",true, vm.$t("Alert"));
                  vm.loadData("activities");
                } else {
                  vm.ShowAlert(vm.$t('DeletedError', [vm.$t('CONTRACTS')]), "failure", true, vm.$t("Alert"));
                }                 
              })
            }
            
            });
        }      
    },
        openActivityModal(item) {           
            if (item.activity_type == "Task") {
                this.showAddTaskModal = true;
                this.modalTaskItem = item;
            }
            else if (item.activity_type == "FollowUp") {
                this.showAddEventModal = true;
                this.modalFollowUpItem = item;
            }
            else if (item.activity_type == "Call") {
                this.showCallModal = true;
                this.modalCallItem = item;
            }
        },
        DisableButton: function () {
            var VueObj = this;
            VueObj.isActivities = false;
            VueObj.isNotes = false;
            VueObj.isAttachment = false;
        },
        openShowNotePopup: function () {
            var ths = this;
            ths.isShowNotePopup = true;
            $("#tab-notes").trigger("click");
            ths.loadData("notes");
        },
        openCallEvent: function () {
            this.showCallModal = (this.showCallModal) ? false : true;
        },
        openAddTaskModel: function () {
            this.showAddTaskModal = (this.showAddTaskModal) ? false : true;
        },
        openCRMAttachmentModal: function () {
            this.showAttachmentModal = (this.showAttachmentModal) ? false : true;      
        },
        openAddNewEvent: function () {
            this.showAddEventModal = (this.showAddEventModal) ? false : true;
        },
        FetchData: function () {
                var VueObj = this;
                VueObj.isLoading=true;
                VueObj.timelinedata = [];                 
                PriceBookDataService.FormLayoutView(VueObj.ApiURL).then(function (response) { 
                    VueObj.isLoading=false;
                    var fullLeftJSON = response.data.LEFTDATA;
                    var stausName = $.grep(fullLeftJSON, function (element, index) {
                    return element.name == 'status_id';
                });
                if (stausName != null && stausName.length > 0) {
                    VueObj.stausName = stausName[0];
                }
                fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
                    return element.form_field_visibility == 'YES';
                });                              
                if (response.data.RIGHTDATA != null && response.data.RIGHTDATA.length > 0) {
                    var fullJSON = response.data.RIGHTDATA;
                    var gruopBy = "";
                    $.each(fullJSON, $.proxy(function (index, item) {
                        gruopBy = VueObj.$options.filters.formatDate(item.Complete_datetime);                                
                        var chkGroup = $.grep(VueObj.timelinedata, function (element, index) {
                            return element.groupKey == gruopBy;
                        });
                        if (chkGroup.length == 0) {                                       
                            var returnedData = $.grep(fullJSON, function (element, index) {
                                return VueObj.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                            });
                        if (returnedData.length > 0) {
                            var objGroup = {
                                groupKey: gruopBy,
                                groupData: returnedData
                            };
                            VueObj.timelinedata.push(objGroup);
                        }
                        }
                    }, VueObj));
                  
                    VueObj.noRecordForTab = false;
                } else {
                    VueObj.noRecordForTab = true;
                }
                if (fullLeftJSON != null && fullLeftJSON.length > 0) {
                    var groupId = fullLeftJSON[0].group_id;
                    $.each(fullLeftJSON, $.proxy(function (index, item) {                                    
                        if (item.name == 'price_book_name') {
                            VueObj.priceBookName = item.value;
                        }
                        if (item.name == "price_book_model") {
                            if (item.value == "" || item.value == null || item.value == "undefined") {
                                item.value = "";
                            }
                            else {
                                item.value = (item.value == 1 ? 'FIX' : 'DIFFERENTIAL');
                            }
                        }
                        var chkGroup = $.grep(this.LeftViewData, function (element, index) {
                            return element.group_id == groupId;
                        });
                        if (chkGroup.length == 0) {
                            var returnedData = $.grep(fullLeftJSON, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (returnedData.length > 0) {
                                var objGroup = {
                                    group_id: groupId,
                                    group_name: returnedData[0].group_name,
                                    LeftViewData: returnedData
                                };
                                VueObj.LeftViewData.push(objGroup);
                            }
                        }
                        groupId = item.group_id;
                    }, VueObj));
                } 
                VueObj.isDetailPage = true; 
            });
        },
        loadData: function (tabName,callFrom) {
                var vm = this;
                vm.DisableButton();
                vm.isDetailPage = false;
                if (tabName == "notes") {
                    vm.activeKey = 2;   
                    vm.isNotes = true;
            if (callFrom && callFrom == "savenote") vm.isShowNotePopup = false;
                }
                else if(tabName=="timeline")
                {
                    vm.activeKey = 1;
                    vm.isDetailPage = true;
                }
                else if (tabName == "attachments") {
                    vm.activeKey = 3;
                    vm.isAttachment = true;
                }
                else if (tabName == "activities") {
                    vm.activeKey = 4;
                    vm.isActivities = true;
                }
                else if (tabName == "products") {
                    vm.activeKey = 5;
                    vm.isProduct = true;
                }
                if (tabName != "" && tabName != null) {    
                    if (tabName == "attachments") {              
                        PriceBookDataService.AttachmentListing(vm.ApiURL).then(function (response) {
                            if (response.data != null) {
                                vm.attachmentData = response.data.data;
                               
                                vm.noRecordForTab = false;
                            } else {
                                vm.noRecordForTab = true;
                            }
                        });
                    }
                    else if (tabName == "activities") {
                        PriceBookDataService.ActivityListing(vm.ApiURL).then(function (response) {
                            if (response.data != null && response.data.data.length > 0) {
                                vm.activitiesData = response.data.data;
                                vm.noRecordForTab = false;
                            } else {
                                vm.noRecordForTab = true;
                            }
                        });
                    }
                    else if (tabName == "timeline") {
                       
                       PriceBookDataService.TimelineListing(vm.ApiURL).then(function (response) {
                            if (response.data != null) {
                                vm.timelinedata = [];
                                var fullJSON = response.data.data;
                                var gruopBy = "";
                                $.each(fullJSON, $.proxy(function (index, item) {
                                    gruopBy =vm.$options.filters.formatDate(item.Complete_datetime);   
                                    var chkGroup = $.grep(vm.timelinedata, function (element, index) {
                                        return element.groupKey == gruopBy;
                                    });
                                    if (chkGroup.length == 0) {
                                        var returnedData = $.grep(fullJSON, function (element, index) {
                                            return vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                                        });
                                        if (returnedData.length > 0) {
                                            var objGroup = {
                                                groupKey: gruopBy,
                                                groupData: returnedData
                                            };
                                            vm.timelinedata.push(objGroup);
                                        }
                                    }
                                }, vm));                               
                                setTimeout(function(){vm.CheckBoxBootstrap()
                            }, 500);
                                vm.noRecordForTab = false;
                            } else {
                                vm.noRecordForTab = true;
                            }
                        });
                    }
                    else if (tabName == "notes") {                       
                        vm.noteData = []; 
                        PriceBookDataService.NoteListing(vm.ApiURL).then(function (response) {
                            if (response.data != null && response.data.data.length > 0) {
                                vm.noteData = response.data.data;  
                                setTimeout(function () {
                                    vm.CheckBoxBootstrap();
                                  }, 500);
                                  setTimeout(function () {
                                    vm.setHeaderForSelectedTab();
                                  }, 500);
                                  setTimeout(function () {
                                    vm.bindLeftTabEventForSetHeading();
                                  }, 500);                           
                                vm.noRecordForTab = false;
                            } else {
                                setTimeout(function () {
                                       vm.setHeaderForSelectedTab();
                                     }, 500);
                                     setTimeout(function () {
                                       vm.bindLeftTabEventForSetHeading();
                                     }, 500);
                                vm.noRecordForTab = true;
                            }
                        });
                    }
                    else if (tabName == "products") {
                        var url = `id=${vm.$route.params.id}&subModuleCode=${vm.$route.params.subModuleCode}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
                        PriceBookDataService.getItemListForAssociate(url).then(function (response) {                          
                            if (response.data != null) {
                                vm.productData = response.data.data;
                                vm.productHeaders = response.data.schema;                         
                                vm.noRecordForTab = false;
                            } else {
                                vm.noRecordForTab = true;
                                vm.productData = null;
                            }
                        });
                    }                    
                }
            }
    }
 
}
</script>
